<template>
  <div class="pop">
    <van-popup
      v-model="show"
      position="bottom"
      closeable
      safe-area-inset-bottom
    >
      <div class="content group">
        <ul class="group-list padding-lr gruop-current">
          <li>
            <div class="user-photo">
              <img src alt />
            </div>
            <div class="user-name">
              <div>玛丽莲梦露</div>
              <div>
                <van-count-down
                  millisecond
                  :time="time"
                  format="仅剩   HH:mm:ss"
                />
              </div>
            </div>
            <div class="schedule">
              <div>
                <van-progress
                  pivot-text="100%"
                  color="#f2826a"
                  :percentage="8"
                />
              </div>
            </div>
            <div class="join"></div>
          </li>
        </ul>
        <div class="product-intro">
          <div class="photo">
            <img src alt />
          </div>
          <div class="intro">
            <div class="name">2020夏季最新热卖爆款小星星项链</div>
            <div class="price">¥89.9-¥129.8</div>
            <div class="point">注意：最低采购量为100件</div>
          </div>
        </div>

        <div class="scroll-view">
          <div class="class-type">
            <van-tabs animated duration="0.2"  >
              <van-tab title="标签 1" name="a">
                <ul class="row-stepper">
                  <li v-for="item in 18" :key="item">
                    <div>xs</div>
                    <div class="number">
                      <div class="stock">库存：{{ item }}</div>
                      <div>
                        <van-stepper v-model="step[item]" />
                      </div>
                    </div>
                  </li>
                </ul>
              </van-tab>
              <van-tab title="333" name="b">内容 2</van-tab>
              <van-tab title="标签 3" name="c">内容 3</van-tab>
              <van-tab title="标签 4" name="d">内容 3</van-tab>
              <van-tab title="标签 5" name="f">内容 3</van-tab>
            </van-tabs>
          </div>
          <div class="recom-group-wrap">
            <div class="recom-title">
              <div>其他拼团</div>
              <div>换一批</div>
            </div>
            <div class="padding">
              <ul class="group-list padding-lr recom-gruop">
                <li>
                  <div class="user-photo">
                    <img src alt />
                  </div>
                  <div class="user-name">
                    <div>玛丽莲梦露</div>
                    <div>
                      <van-count-down
                        millisecond
                        :time="time"
                        format="仅剩   HH:mm:ss"
                      />
                    </div>
                  </div>
                  <div class="schedule">
                    <div>
                      <van-progress
                        pivot-text="100%"
                        color="#f2826a"
                        :percentage="8"
                      />
                    </div>
                  </div>
                  <div class="join">
                    <button>拼单</button>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </div>

        <div class="count">
          已选择：
          <span>300件</span>
        </div>
        <div class="buy-wrap">
          <div class="type-1" v-if="false">立即购买</div>
          <div class="type-2">
            <div class="left-c">
              <div>¥99999/件</div>
              <div>单独购买</div>
            </div>
            <div class="right-c">
              <div>¥99999/件</div>
              <div>发起团购</div>
            </div>
          </div>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import { Popup, Progress, CountDown, Tab, Tabs, Stepper } from "vant";
export default {
  components: {
    [Progress.name]: Progress,
    [CountDown.name]: CountDown,
    [Popup.name]: Popup,
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [Stepper.name]: Stepper
  },
  data() {
    return {
      closeImg: require("@/assets/close@2x.png"),
      show: false,
      step: {},
      time: 30 * 60 * 60 * 1000
    };
  },

  props: {
    value: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    value(val) {
      this.show = val;
    },
    show(val) {
      this.$emit("input", val);
    }
  },
  methods: {
    onTabsScroll(e) {
      console.log(11, e);
      e.isFixed = true;
    }
  }
};
</script>

<style lang="scss">
$gray: #928f92;
$buyColorRed: #f14745;

.van-action-sheet {
  max-height: 95%;
}
.pop {
  .van-popup {
    overflow: hidden;
  }
  .content {
    position: relative;
    padding-top: 20px;
    .close-pop {
      position: absolute;
      z-index: 10;
      top: 15px;
      right: 15px;
      padding: 15px;
      img {
        width: 30px;
        height: 30px;
      }
    }

    .gruop-current {
      @include border-bottom;
    }
    .product-intro {
      padding: 20px 0;
      @include flex;

      .photo {
        width: 200px;
        height: 200px;
        margin: 0 23px 0 30px;
        background: $bg;
      }
      .intro {
        padding-right: 30px;
        .name {
          padding-top: 40px;
          @include text-column(1);
          @include font-size(30);
        }
        .price {
          @include font-size(40);
          padding: 22px 0 31px;
          color: $priceColor;
          line-height: 1;
        }
        .point {
          @include font-size(26);
          line-height: 1;
          color: $orange;
        }
      }
    }
    .scroll-view {
      max-height: 625px;
      min-height: 625px;
      overflow-y: scroll;
      -webkit-overflow-scrolling: touch;
      .van-tabs__wrap {
        height: 74px;
        position: fixed;
        left: 0;
        z-index: 100;
        bottom: 809px;
        bottom: calc(constant(safe-area-inset-bottom) + 735px);
        bottom: calc(env(safe-area-inset-bottom) + 735px);
        min-width: 100%;
        
      }
      .van-tabs__content {
        padding-top: 74px;
      }
      .van-sticky {
        color: $defaultFontColor;

        .van-tab {
          @include font-size(30);
          color: $defaultFontColor;
          .van-tab__text--ellipsis {
            white-space: nowrap;
          }
        }
      }
      .class-type {
        .row-stepper {
          li {
            @include flex(space-between);
            @include font-size(30);
            @include border-bottom;
            height: 120px;
            margin: 0 30px;
            .number {
              flex: 1;
              @include iflex(flex-end);

              .stock {
                color: $gray;
                margin-right: 30px;
              }
            }
          }
        }
      }

      .recom-group-wrap {
        .recom-title {
          padding: 20px 30px;
          display: flex;
          justify-content: space-between;
          line-height: 1;
          > div {
            &:nth-child(2) {
              color: $gray;
              @include font-size(26);
            }
          }
        }

        .recom-gruop {
          background: #fafafa;
          border-radius: 10px;
          .user-photo {
            .user-photo {
              width: 78px;
              height: 78px;
            }
          }
        }
      }
    }
    .count {
      height: 64px;
      @include flex;
      color: $gray;
      span {
        color: $orange;
      }
    }
    .buy-wrap {
      height: 120px;
      @include font-size(32);
      .type-1 {
        background: $buyColorRed;
        height: 120px;
        @include flex;
        color: #fff;
      }
      .type-2 {
        @include flex;
        @include border-top;
        > div {
          height: 120px;
          @include flex(center, center, column);
          flex: 1;
          > div {
            line-height: 1;
            &:first-child {
              margin-bottom: 10px;
            }
          }
          &:first-child {
            color: $buyColorRed;
          }
          &:nth-child(2) {
            color: #fff;
            background: $buyColorRed;
          }
        }
      }
    }
  }
  [class*=van-hairline]::after{
    border-top:transparent;
  }
}
</style>
